.App {
  text-align: center;
}

.tagInput {
  margin: auto;
  margin-left: 10px;
  margin-bottom: 10px;
  border: none;
  padding: 0;
  width: 12ch;
  background: repeating-linear-gradient(90deg, #696969 0, #696969 1ch, transparent 0, transparent 1.5ch) 0 100%/11.5ch 2px no-repeat;
  font: 5ch droid sans mono, consolas, monospace;
  letter-spacing: 0.5ch;
  color: #ffffff;
}

.tagInput::placeholder {
  color: #B4B4B4;
}

.tagInput:focus {
  outline: none;
  color: #ffffff;
}

.inputButton {
  padding: 10px;
  width: 240px;
  margin: 5px;
  background-color: #2d313a;
  border: none;
  color: #B4B4B4;
  border-radius: 5px;
  font-family: Rubik,sans-serif;
  font-size: 25px;
}

.inputButton:hover {
  background-color: #3d4149;
}

.userList {
  text-align: center;
  margin: auto;
  padding: 10px;
  width: 250px;
  background-color: #3d4149;
  border-radius: 10px;
}

.users {
  list-style-type: none;
  display: inline;
  color: #e9eaea;
  font-family: Rubik,sans-serif;
  font-size: 20px;
  font-weight: bold;
}

.user {
  width: 100px;
  background-color: #3d4149;
}

.header {
  text-align: left;
  padding: 20px;
  background-color: #208e2c;
  color: #e9eaea;
  font-family: Rubik,sans-serif;
  font-size: 20px;
  font-weight: bold;
}

.body {
  text-align: center;
}

.inputs {
  padding: 100px;
}

.graphs {
  padding: 100px;
}

.charts {
  text-align: center;
  align-content: stretch;
  width: 96%;
  display: flex;
  margin: 10px;
}

.sideCharts {
  flex: 33%;
}

.mainChart {
  padding-top: 2%;
  flex: 63%;
}

.spacing {
  margin-top: -100px;
}